import "./modules/Animate";
import "./modules/ToggleNav";
import "./modules/custom-selectbox";
import "./modules/cookies";

var swiper = new Swiper(".references__slider", {
        slidesPerView: 1,
        spaceBetween: 10,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
        breakpoints: {
            500: {
              slidesPerView: 2,
              spaceBetween: 20,
            },
          767: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          991: {
            slidesPerView: 2,
            spaceBetween: 40,
          },
          1200: {
        slidesPerView: 4,
        spaceBetween: 65,
     },
    },
});

$('#file-upload').change(function() {
  var i = $(this).prev('label').clone();
  var file = $('#file-upload')[0].files[0].name;
  $(this).prev('label').text(file);
});

$(document).ready(function() {
	$.cookieConsent({
        message: "Tato stránka používá cookies. Užíváním této stránky souhlasíte s naším používáním těchto cookies.",
        style: "background: #eaeaea;",
        consentMessage: "Rozumím",
        consentStyle: "background: #26a37a; border-radius: 50px; font-size: 14px; cursor: pointer;"
    });
});
